import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    NavLink
} from "react-router-dom";

import { useSelector, useDispatch } from 'react-redux'
import { showTopNotification, hideTopNotification } from './features/settings/settingsSlice'
import Logo from './components/Logo'
import { useLocation } from "react-router-dom";

function Nav() {
    const location = useLocation();

    //destructuring pathname from location
    const { pathname } = location;

    //Javascript split method to get the name of the path in array
    const splitLocation = pathname.split("/");
    console.log(splitLocation[1]);
    return (<div class="space-x-2 flex-1 text-right">
        <Link to="/collection" className={splitLocation[1] === "collection" ? "header-link-active" : "header-link"}>Collection</Link>
        <Link to="/roadmap" className={splitLocation[1] === "roadmap" ? "header-link-active" : "header-link"}>Roadmap</Link>
        <Link to="/faq" className={splitLocation[1] === "faq" ? "header-link-active" : "header-link"}>FAQ</Link>
        <Link to="/about" className={splitLocation[1] === "about" ? "header-link-active" : "header-link"}>About
            us</Link>
        <Link to="/verification" className={splitLocation[1] === "verification" ? "header-link-active" : "header-link"}>Verification</Link>
        <Link to="/buy" className={splitLocation[1] === "buy" ? "header-link-active hidden" : "header-link-buy hidden"}>Buy a Galgo</Link>
    </div>);
}

function Header() {
    const topNotificationState = useSelector((state) => state.settings.topNotification)
    const dispatch = useDispatch()

    return (
        <div>
            {topNotificationState.show &&
                <div className="bg-green-600 p-4 text-white text-center font-bold">
                    Pre-sale phase 1 is open! Enjoy!
                </div>}
            <div>
            </div>
            <div className="bg-gray-900">
                <div className="flex items-center max-w-7xl mx-auto border-b-2 border-gray-400 p-6">
                    <Logo />
                    <Nav />
                </div>
            </div>
        </div>

    );
}

export default Header;