import { createSlice } from '@reduxjs/toolkit'
import { v4 as uuidv4 } from 'uuid';

const getFingerprint = () => {
    let fingerprint = localStorage.getItem('fingerprint');
    if (!fingerprint) {
        fingerprint = uuidv4()
        localStorage.setItem('fingerprint', fingerprint)
    }
    return fingerprint;
}

export const settingsSlice = createSlice ({
    name: 'settings',
    initialState: {
        fingerprint: getFingerprint(),
        topNotification: {
            message: 'Pre-sale phase 1 is open. You can start collection your Galgos!',
            show: false
        },
    },
    reducers: {
        showTopNotification: (state) => {
            state.topNotification.show = true;
        },
        hideTopNotification: (state) => {
            state.topNotification.show = false;
        },
    },
})

export const { showTopNotification, hideTopNotification } = settingsSlice.actions

export default settingsSlice.reducer