import { useState, useRef } from 'react';

function FaqItem(props) {

    const [expanded, setExpanded] = useState(props.expanded);
    const toggle = () => {
        setExpanded(!expanded);
    }
    const elem = useRef(null);
    const getStyle = (isExpanded) => {
        return isExpanded ? {
            "max-height": elem.current.scrollHeight
        } : {};
    }

    return (
        <div className="border-t border-gray-700 py-6">
            <div>
                <a href="#" className="transition-all hover:text-blue-400 text-white flex justify-between items-center mr-4 text-2xl font-bold" onClick={toggle}>
                    <div>{props.title}</div>
                    {expanded
                        ? <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M18 12H6" />
                        </svg>
                        : <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
                        </svg>
                    }
                </a>
                <div className="transition-all max-h-0 overflow-hidden duration-700" ref={elem} style={getStyle(expanded)}>
                    <div className="pt-6"></div>
                    {props.children}
                </div>
            </div>

        </div>)
}

function Faq() {
    return (
        <div className="bg-gray-900">
            <div className="text-gray-400 mx-auto max-w-7xl p-6 pt-20">
                <h2 className="text-4xl text-white font-extrabold mb-10">Frequently Asked Questions</h2>
                <div className="py-10">
                    <FaqItem title="Which wallet can I use?" expanded={false} >
                        <p className="mb-6">
                            <ul className="list-disc list-inside">
                                <li><a href="https://yoroi-wallet.com/">NamiWallet</a></li>
                                <li><a href="https://yoroi-wallet.com/">Yoroio</a></li>
                                <li><a href="https://daedaluswallet.io/">Daedalus</a></li>
                                <li><a href="https://adalite.io/">AdaLite (Hardware wallet recommended)</a></li>
                            </ul>
                        </p>
                        <p className="mt-4">Never send ADA from an exchange!</p>
                    </FaqItem>
                    <FaqItem title="What is your return and refund policy?" expanded={false} >
                        <p>We gladly accept returns for any of the following reasons:</p>
                        <p><ul><li>We sent the incorrect NFT</li>
                            <li>Your purchase was confirmed on-chain but you did not receive your NFT</li></ul></p>
                        <p>NOTE: If you bought a Galgo and used an exchange address as your receiving address then it is unfortunately not possible to retrieve your NFT.</p>
                    </FaqItem>
                    <FaqItem title="What levels of rarity exist within The Galgos?" expanded={false} >
                        <p>
                            <h4 class="font-bold">Levels of Rarity Include:</h4>

                            <ul>
                                <li>Common: 55+ Produced</li>
                                <li>Uncommon: 21-34 Produced</li>
                                <li>Rare: 8-13 Produced</li>
                                <li>Ultra-Rare: 3-5 Produced</li>
                                <li>Epic: 1-2 Produced</li>
                            </ul>
                        </p>
                        <p class="mt-6">Note: Every Galgo pack consists of different hand drawn characters with varying rarities. Some of the Epic and Ultra-Rare will be sold at auction on it's respective reveal date. Follow us on Social Media to keep up-to-date.</p>



                    </FaqItem>





                </div>
            </div>
        </div>)
}

export default Faq;