import { Link } from 'react-router-dom'

function Logo({ size }) {
    size = size || 'normal';
    return (<Link to="/">
        <div className={"flex text-gray-50 items-center gap-4"}>
            <div className={size === 'normal' ? "w-16 h-16" : "w-10 h-10"}>
                <img src="/images/logo-200x200.png" className="object-cover rounded-full" />
            </div>
            <span className={size === 'normal' ? "brand-text text-5xl" : "brand-text text-3xl"}>The Galgos</span>
        </div></Link>
    );

}

export default Logo