import logo from './logo.svg';
import Faq from './Faq';
import Footer from './Footer';
import Header from './Header';
import About from './About';
import Verification from './Verification';
import Home from './Home';
import Collection from './components/Collection';
import Roadmap from './Roadmap';
import NftDetails from './NftDetails';
import Buy from './Buy';
import './App.css';


import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

import { useQuery, useMutation, useSubscription, gql } from '@apollo/client';
const EXCHANGE_RATES = gql`
query nfts {
  nft(limit: 10, order_by: {name: asc}) {
    id
    name
    price
    reserved_by
    reserve_expires_at
  }
}`;

// TODO: Implement a mutation on the server side just for the reservation.

const UPDATE_NFT = gql`
mutation updateNftByPk ($id: numeric!, $reserved_by: String!){
  update_nft_by_pk(pk_columns: {id: $id}, _set: {reserved_by: $reserved_by}) {
    id
    reserved_by
  }
}
`;

const RESERVATION_SUBSCRIPTION = gql`
subscription OnReservation {
  nft {
    id
    reserved_by
  }
}
`


function App() {
  return (
    <Router>
      <Header />
      <Switch>
        <Route path="/verification">
          <Verification />
        </Route>
        <Route path="/super">
          <Verification />
        </Route>
        <Route path="/collection">
          <Collection />
        </Route>
        <Route path="/about">
          <About />
        </Route>
        <Route path="/faq">
          <Faq />
        </Route>
        <Route path="/roadmap">
          <Roadmap />
        </Route>
        <Route path="/galgo/:id">
          <NftDetails />
        </Route>
        <Route path="/buy">
          <Buy />
        </Route>
        <Route path="/">
          <Home />
        </Route>
      </Switch>
      <Footer />
    </Router>
  );
}

export default App;
