import { gql } from '@apollo/client'

const SET_RESERVATION = gql`
mutation updateNftByPk ($id: Int!, $reserved_by: String!){
  update_nft_by_pk(pk_columns: {id: $id}, _set: {reserved_by: $reserved_by}) {
    id
    reserved_by
  }
}`;

const ADD_RESERVATION = gql`
mutation insertReservation ($id: Int!, $reserved_by: String!){
  insert_reservation_one(object: {nft_id: $id, reserved_by: $reserved_by}) {
    id
    reservation_expiration_time
    price
    reserved_by
    payment_address
  }
}`;


const GET_ALL_COLLECTIBLES = gql`
query collectibles ($limit: Int!) {
    collectible (order_by: {name: asc}, limit: $limit) {
      id
      name
      number
      given_name
      pack_number
      available
      total_supplay
      image_url
    }
  }`;


const GET_NFT_DETAILS = gql`
query MyQuery ($id: Int!){
    collectible_by_pk(id: $id) {
      id
      given_name
      name
      image_url
      gender
      total_supplay
      available
      pack_number
      traits
      occupation
    }
  }
`;


