function CountdownTimer({ until }) {
    const now = new Date()
    const [remainingSeconds, setRemainingSeconds] = useState(Math.floor((until - now) / 1000));
    const minutes = Math.floor(remainingSeconds / 60);
    const seconds = remainingSeconds - 60 * minutes;
    useEffect(() => {
        const timer = setTimeout(() => {
            if (remainingSeconds <= 0) {
                clearTimeout(timer)
            } else {
                setRemainingSeconds(remainingSeconds - 1);
            }
        }, 1000);
        return () => clearTimeout(timer);
    });
    //WHen the counter reach 0 send and event and set the state
    return <span className="font-bold">{minutes} minutes, {seconds} seconds</span>;
}


