function Content() {
    const { id } = useParams();
    const fingerprint = useSelector((state) => state.settings.fingerprint)
    let { loading, error, data } = useQuery(GET_NFT_DETAILS, { variables: { id, reserved_by: fingerprint } });

    const [details, setDetails] = useState(null);


    if (loading) {
        return (<Loading />)
    }

    if (error) {
        return (<Error />)
    }

    const item = data.collectible_by_pk;

    return <Details item={item} onRefresh={() => { setDetails('blah') }} />;
}

