import { useQuery, useMutation, useSubscription, gql } from '@apollo/client'
import { Link } from 'react-router-dom'
import { GET_ALL_COLLECTIBLES } from '../db/queries';

function CollectionItem({item}) {
    return (<div class="bg-black p-4">
        <Link to={`/galgo/${item.id}`} class="rounded-xl shadow-lg max-w-max mx-auto group overflow-hidden block">
            <img class="transform transition duration-1000 ease-out group-hover:scale-150 w-full" 
            src={`https://res.cloudinary.com/thegalgos/image/upload/w_400/${String(item.number).padStart(4, 0)}.png`} />
        </Link>
        <div class="mt-4 flex items-center justify-between  text-white">
            <span class="font-bold text-md">{item.given_name}</span>
            <span class="text-xs border-gray-500 border rounded-md px-4 py-2">Series {item.pack_number}</span>
            <span class="hidden text-xs border-gray-500 border rounded-md px-4 py-2">addr1qyvf...asqwhjmt5</span>

        </div>
        <div class="mt-2 flex justify-between text-white items-center">
            {
                item.available > 0
                    ? <Link to={`/galgo/${item.id}`} className="transition block shadow-lg bg-blue-800 py-2 px-6 text-sm rounded-full hover:bg-blue-900">Details</Link>
                    : <span class="shadow-lg bg-yellow-800 px-6 text-sm rounded-full">Sold Out</span>
            }
            <span class="text-sm hidden border-white py-4 font-bold"></span>
        </div>


    </div>);
}

function randomize(data) {
    const result = []
    const n = data.length
    for (let i = 0; i < n; i++) {
        const index = Math.round(Math.random() * (data.length-1));
        const item = data.splice(index, 1)[0]
        console.log(item)
        result.push(item)
    }
    return result
}

function Collection({ title, maxResults }) {
    const limit = maxResults || 10000 // TODO: No limit.    
    let { loading, error, data } = useQuery(GET_ALL_COLLECTIBLES, { variables: { limit } });
    
    if (loading) {
        return <p>Loading...</p>
    }

    if (error) {
        return <div className="bg-gray-900">
            <div className="text-gray-400 mx-auto max-w-7xl p-10">
                <h1 class="text-xl text-bold">Ups!</h1>
                <p class="py-6">Our servers are busy. Please try again later.</p>
            </div></div>
    }

    data = [].concat(data.collectible)
    //data = randomize(data)
    title = title || "Collection"


    return (
        <div className="bg-gray-900">
            <div className="text-gray-400 mx-auto max-w-7xl p-6 pt-20">
                <h2 className="text-4xl text-white font-extrabold mb-10">{title}</h2>

                <div class="md:max-w-7xl mx-auto grid gap-10 grid-rows-1 grid-cols-1 sm:grid-cols-2 xl:grid-cols-4">
                    {data.map((item) => <CollectionItem key={item.id} item={item} />)}
                </div>
            </div>

        </div>

    );
}

export default Collection;